import React, { Component } from 'react';
import { connect } from 'react-redux';
// import { Link } from 'react-router-dom';
import '../../pages/Home/css/Home.css'
import bitcoinImg from '../../assets/img/bitcoin-small.png';
import coinsImg from '../../assets/img/coins.png';

import { loadUsers } from '../../actions/UserActions.js';
import { getRate } from '../../actions/BitcoinActions.js';

class Home extends Component {
  state = {

  };
  componentDidMount() {
    this.props.loadUsers();
    this.props.getRate({ symbol: 'USD', amount: 1 });
  }

  toContacts = () => {
    this.props.history.push('/contacts/');
  }

  render() {
    const { users, loggedInUser, rate } = this.props
    return (
      <div className="home flex">
        {users && loggedInUser && <div className="user-info-container flex row">
          <div className=" flex col">
            <span className="user-name-field">Hello {loggedInUser.username}</span>
            <div className="user-amount-container flex row">
              <img className="coins-img" alt="bitcoin" src={coinsImg}></img>
              <span className="user-amount-field">Coins: {loggedInUser.amount}</span>
            </div>
            <div className="bitcoin-rate-container flex row">
              <span className="bitcoin-rate" >BTC: {rate}</span>
              <img className="bitcoin-img" alt="bitcoin" src={bitcoinImg}></img>
            </div>
          </div>
          <button className="contacts-btn" onClick={this.toContacts}>Contacts</button>
        </div>}
        {users && !loggedInUser && <div className="guest-login">
          <span className="guest-login-msg">Please login</span>
        </div>}
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    users: state.user.users,
    loggedInUser: state.user.loggedInUser,
    rate: state.bitcoin.rate
  };
};
const mapDispatchToProps = {
  loadUsers, getRate
};

export default connect(mapStateToProps, mapDispatchToProps)(Home);
