import ContactService from '../services/ContactService';

export function loadContacts(filterBy) {
    return async dispatch => {
        try {
            const contacts = await ContactService.getContacts(filterBy);
            dispatch(setContacts(contacts));

        } catch (err) {
            console.log('ContactActions: err in loadContacts', err);
        }
    };
}

export function loadSelectedContact(contactId) {
    return async dispatch => {
        try {
            const contact = await ContactService.getContactById(contactId);
            dispatch(setSelectedContact(contact));
        } catch (err) {
            console.log('ContactActions: err in loadContact', err);
        }
    };
}

export function addContact(contact) {
    return async dispatch => {
        try {
            const addedContact = await ContactService.add(contact);
            dispatch(_addContact(addedContact));
        } catch (err) {
            console.log('ContactActions: err in addContact', err);
        }
    };
}

function setContacts(contacts) {
    return {
        type: 'SET_CONTACTS',
        contacts
    };
}
function setSelectedContact(contact) {
    return {
        type: 'SET_SELECTED_CONTACT',
        contact
    };
}
function _addContact(contact) {
    return {
        type: 'CONTACT_ADD',
        contact
    };
}
