import React, { Component } from 'react'
import './css/ContactFilter.css'

export default class ContactFilter extends Component {

    state = {
        filterBy: {
            term: ''
        }
    }
    onFilterBy = (ev) => {
        const value = ev.target.value;
        (value === '') ? this.props.filterContacts() :
            this.setState({ filterBy: { term: value } }, () => this.props.filterContacts(this.state.filterBy))
        console.log('this.state.filterBy: ', this.state.filterBy.term)
    }
    render() {
        return <input className="search-input" type="text" name="term" placeholder="find..." onChange={this.onFilterBy} onKeyDown={this.onFilterBy}></input>
    }
}