import React, { Component } from 'react';
import './../ContactPreview/css/ContactPreview.css'

export default class ContactPreview extends Component {
    onSelectContact = (contactId) => {
        if (this.props.onSelectContact)
            this.props.onSelectContact(contactId);
    }
    render() {
        const { contact } = this.props;
        return <li className="contact-list-item flex row" onClick={() => this.onSelectContact(contact.Id)}>
            <img className="contact-img" alt="contact pic" src={contact.imgUrl} />
            <h2 className="contact-name">{contact.name}</h2>
        </li>
    }
}