
import React from 'react';
import { Link } from 'react-router-dom';
import { withRouter } from "react-router";
import logo from '../cmps/Nav/img/Logo.png'
import loginImg from '../cmps/Nav/img/login.png'
import '../cmps/Nav/css/Nav.css'

function Nav() {
    return <nav className="nav flex row">
        <div className="nav-container flex row">
            <div className="logo-container nav-item flex row">
                <Link className="nav-link" to="/"><img className="Logo-img" alt="Logo" src={logo}></img></Link>
                <Link className="nav-link" to="/"><span className="logo-text" ><span className="logo-separator">|</span> Mr. Bitcoin</span></Link>
            </div>


            <div className="login-container nav-item">
                <Link className="nav-link" to="/login">
                    <img className="login-img" alt="login" src={loginImg}></img>
                </Link>
            </div>
        </div>

    </nav>
}
export default withRouter(Nav)