import Axios from "axios";
import history from '../history';

export default {
    getRate,
    getMarketPrice
}


async function getRate(coin) {
    try {
        const res = await Axios.get(`https://blockchain.info/tobtc?currency=${coin.symbol}&value=${coin.amount}`)
        return res.data;
    } catch (err) {
        console.log(`Had Issues getting bitcoin rate-  ${err}`);
        console.dir(err);
        if (err.response && err.response.status === 401) {
            history.push('/'); // diaspatch ('authorition error')
        }
        throw err;
    }
}



