import React, { Component } from 'react'
import ContactPreview from "../ContactPreview/ContactPreview";
import "./css/ContactList.css";

export default class ContactList extends Component {

    render() {
        const { contacts, onSelectContact } = this.props
        return (
            <div className="contact-list-container flex">
                <ul className="contact-list">{contacts.map((contact) =>
                    <ContactPreview key={contact._id} contact={contact} onSelectContact={() => onSelectContact(contact._id)}></ContactPreview>)}
                </ul>
            </div>
        )
    }
}

