import React from 'react';
import { Router, Switch, Route } from 'react-router';
import history from './history';
import '../src/assets/css/Global/Global.css';

// Pages
import About from './pages/About.js';
import Login from './pages/Login.js';
import HomePage from './pages/Home/Home.js';
import ContactPage from './pages/ContactPage/ContactPage.js';
import ContactDetailsPage from './pages/ContactDetailsPage.js';
// Components
import Nav from './cmps/Nav';




function App() {
  return (
    <div className="App">
      <Router history={history}>
        <Nav />
        <Switch>
          <Route path="/" component={HomePage} exact />
          <Route path="/" component={About} exact />
          <Route path="/about" component={About} exact />
          <Route path="/login" component={Login} exact />
          <Route path="/contacts" component={ContactPage} exact />
          <Route path="/contact/:id" component={ContactDetailsPage} exact />
        </Switch>
      </Router>
    </div>
  );
}

export default App;
