import React, { Component } from 'react';
import { connect } from 'react-redux';

import { loadSelectedContact } from '../actions/ContactActions.js';
import { loadUsers } from '../actions/UserActions.js';
import './ContactDetailsPage/css/ContactDetailsPage.css'

class ContactDetailsPage extends Component {
    state = {

    };
    componentDidMount() {
        this.props.loadUsers();
    }

    render() {
        const { selectedContact } = this.props
        return (
            <div className="contact-details-container flex col">

                <span className="contact-info-txt contact-name">{selectedContact.name}</span>
                <span className="contact-info-txt contact-mail">{selectedContact.email}</span>
                <span className="contact-info-txt contact-phone">{selectedContact.phone}</span>
                <img className="contact-info-img" src={selectedContact.imgUrl} />
            </div>
        );
    }
}

const mapStateToProps = state => {
    return {
        users: state.user.users,
        loggedInUser: state.user.loggedInUser,
        selectedContact: state.contact.selectedContact
    };
};
const mapDispatchToProps = {
    loadUsers,
    loadSelectedContact
};

export default connect(mapStateToProps, mapDispatchToProps)(ContactDetailsPage);
