import React, { Component } from 'react';
import { connect } from 'react-redux';
import './css/ContactPage.css'

// Actions
import { loadUsers } from '../../actions/UserActions.js';
import { loadContacts, loadSelectedContact } from '../../actions/ContactActions.js';

// Components
import ContactFilter from '../../cmps/ContactFilter/ContactFilter'
import ContactList from '../../cmps/ContactList/ContactList.jsx'

class ContactPage extends Component {
    state = {
        selectedContact: { name: '' }
    }
    componentDidMount() {
        this.props.loadContacts();
        this.props.loadUsers();
    }

    filterContacts = (filterBy) => {
        (filterBy) ? this.props.loadContacts(filterBy)
            : this.props.loadContacts()
    }
    onSelectContact = (contactId) => {
        this.props.loadSelectedContact(contactId)
            .then(() => {
                this.props.history.push(`/contact/${this.props.selectedContact._id}`);
            })
    }

    render() {
        const { contacts } = this.props
        return (
            <section className="contacts-page-container">
                <h2>Contacts</h2>
                <ContactFilter filterContacts={this.filterContacts}></ContactFilter>
                <ContactList contacts={contacts} onSelectContact={this.onSelectContact}></ContactList>
            </section>
        );
    }
}

const mapStateToProps = state => {
    return {
        users: state.user.users,
        loggedInUser: state.user.loggedInUser,
        contacts: state.contact.contacts,
        selectedContact: state.contact.selectedContact
    };
};
const mapDispatchToProps = {
    loadUsers,
    loadContacts,
    loadSelectedContact
};

export default connect(mapStateToProps, mapDispatchToProps)(ContactPage);
